/* Background pattern from Subtle Patterns */
html, body {
  padding: 0;
  margin: 0;
  font-family: "Avenir", "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
}
* {
  box-sizing: border-box;
}
