@media (min-width: 1101px) {
.card {
    width: calc(100% / 3);
}
}
h1 {
  font-size: 38px;
  margin: 10px 0px;
}
h2 {
  font-size: 30px;
  margin: 10px 0px;
}
h3 {
  font-size: 26px;
  margin: 10px 0px;
}
h4 {
  font-size: 22px;
  margin: 5px 0px;
}



